<template>
<section>


    <!--  -->
    <Modal :title="previewPaperTitle +'('+ previewPaperType+')'" width="900" v-model="showPreviewDialog" @on-ok="clear('preview')">
      <Table :columns="columns1" :data="studentList"></Table>
    </Modal>

    <!--  -->
    <Modal width="700" :title="studentName+ ''"
    v-model="shwoDetailDialog"
    @on-ok="clear('detail')"
    @on-cancel="shwoDetailDialog=false">

    <div class="answer" v-for="item in detail" v-bind:key="item.question.studentId">
              <h3 class="question-type" v-if="item.question.type==1"></h3>
              <h3 class="question-type" v-if="item.question.type==2"></h3>
              <h3 class="question-type" v-if="item.question.type==3"></h3>
              <h3 class="question-type" v-if="item.question.type==4"></h3>

          <h3>{{item.question.content}}</h3>

          <!--  -->
            <ul v-if="item.question.type==1" class="unselect">
            <li><span class="abc">A</span>{{item.question.a}}</li>
            <li><span class="abc">B</span>{{item.question.b}}</li>
            <li><span class="abc">C</span>{{item.question.c}}</li>
            <li><span class="abc">D</span>{{item.question.d}}</li>
            </ul>

    <p>:{{item.question.key}}</p>   
    <p>:{{item.studentAnswer}}</p>
    <i>:{{item.question.analysis}}</i>
    <p style="color:red">:{{item.socre}}</p>
    </div>
    </Modal>

<!--  -->
    <Modal width="700" :title="'' + studentName + ''"
    v-model="showCheckDialog"
   >

      <div class-name="subjectAnswer" v-for="item in subject" v-bind:key="item.question.questionId">
      <h2>:{{item.question.content}}({{item.question.socre}})</h2>
      <p><span>:</span>{{item.question.key}}</p>
      <p><span>:</span>{{item.studentAnswer}}</p>
      <Input-number :max="item.question.socre" :min="1" :value="1" v-model="subjectScore"></Input-number>
      <Button type="success" @click="toSubmitSubjectScore(item.studentId,item.question.testpaperId,item.question.questionId,subjectScore)"></Button>
      <hr>
    </div>
    </Modal>


        <!--  -->
		<Table border :columns="columns7" :data="pratiseLists[pageNum - 1]"></Table>
		<Page :total="totalPar" :current="pageNum"   @on-change="changeNum" class="pagepra"/>
    	<!-- end -->
</section>
</template>



<script>
// "type": "" , //1 2 3 4 5 6
import { mapState, mapActions } from "vuex";
import user from "../../store/types/user";
import paper from "../../store/types/paper";
import organization from "../../store/types/organization";
import { IP } from "src/utils/interaction";

export default {
  data() {
    return {
      chapterId: null,
      paperType: null,
      testpaperType: null,
      templateURL: IP + "excel/template.xlsx",

      showPreviewDialog: false,
      previewPaperTitle: "",
      previewPaperType: "",

      shwoDetailDialog: false,
      studentName: "",
      detail: [],

      showCheckDialog: false,
      subject: [],
      subjectScore: 1,

      haveSubject: null,

      totalPar: 0,
      columns7: [
        {
          title: "",
          key: "testpaperTitle"
        },
        {
          title: "",
          key: "createTime"
        },
        {
          title: "",
          key: "endingTime"
        },
        {
          title: "",
          key: "action",
          width: 300,
          align: "center",
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "info",
                    size: "small"
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      this.toPreviewPaper(
                        params.row.testpaperId,
                        params.row.testpaperTitle,
                        params.row.testpaperType,
                        params.row.haveSubject
                      );
                    }
                  }
                },
                ""
              )
            ]);
          }
        }
      ],
      pratiseLists: [],
      pageNum: 1,

      columns1: [
        {
          title: "",
          key: "studentNum",
          width: 120
        },
        {
          title: "",
          key: "studentName"
        },
        {
          title: "",
          key: "ifCheck",
          render: (h, params) => {
            if (params.row.ifCheck == 0) {
              return h("div", [h("span", "")]);
            } else {
              return h("div", [h("span", "")]);
            }
          }
        },
        {
          title: "",
          key: "object"
        },
        {
          title: "",
          key: "subject"
        },
        {
          title: "",
          key: "ifAttend",
             render: (h, params) => {
            if (params.row.ifAttend == 0) {
              return h("div", [h("span", "")]);
            } else {
              return h("div", [h("span", "")]);
            }
          }
        },
        {
          title: "",
          key: "testpaper",
          render: (h, params) => {
            return h("div", [h("span", params.row.testpaper.testpaperScore)]);
          }
        },
        {
          title: "",
          key: "action",
          width: 200,
          align: "center",
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "info",
                    size: "small"
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      if (params.row.ifAttend) {
                        this.toTestDeatil(
                          params.row.testpaper.testpaperId,
                          params.row.studentId,
                          params.row.studentName
                        );
                      } else {
                        this.$Message.error("!");
                      }
                    }
                  }
                },
                ""
              ),
              h(
                "Button",
                {
                  props: {
                    type: "success",
                    size: "small"
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      let that = this;
                      if (that.haveSubject == 0) {
                        this.$Message.error("");
                      } else if (params.row.ifAttend == 0) {
                        this.$Message.error("!");
                      } else {
                        this.toCheckSubject(
                          params.row.testpaper.testpaperId,
                          params.row.studentId,
                          params.row.studentName
                        );
                      }
                    }
                  }
                },
                ""
              )
            ]);
          }
        }
      ],
      studentList: []
    };
  },

  computed: {
    ...mapState({
      user: state => state.user,
      organization: state => state.organization,
      paperQuestionList: state => state.paper.paperQuestionList
    })
  },
  methods: {
    ...mapActions(paper.actions),
    getPractice() {
      let that = this;
      this.getExPracticeList({
        organizationId: this.organization.organizationId
      })
        .then(data => {
          let arr = [];
          for (let i = 0; i < data.length; i += 10) {
            let ari = [];
            for (let j = i; j < i + 10; j++) {
              if (!data[j]) break;
              ari.push(data[j]);
            }
            arr.push(ari);
          }
          that.totalPar = data.length;
          that.pratiseLists = arr;
        })
        .catch(err => {
          console.log("error");
        });
    },
    toPreviewPaper(paperID, paperTITLE, paperTYPE, haveSUBJECT) {
      this.previewPaperTitle = paperTITLE;
      this.paperType = paperTYPE;

      this.haveSubject = haveSUBJECT;

      this.checkProgress({
        testpaperId: paperID,
        organizationId: this.organization.organizationId
      })
        .then(data => {
          if (data.info.state) {
            switch (this.paperType) {
              case 1:
                this.previewPaperType = "";
                break;
              case 2:
                this.previewPaperType = "";
                break;
              case 3:
                this.previewPaperType = "";
                break;
            }
            this.studentList = data.info.data;
            this.showPreviewDialog = true;
            this.$Message.success(data.info.stateInfo);
          } else {
            this.$Message.error(data.info.stateInfo);
          }
        })
        .catch(err => {
          this.$Message.error(err);
        });
    },

    //
    toTestDeatil(paperID, studentID, studentNAME) {
      this.studentName = studentNAME;

      this.testDetail({
        testpaperId: paperID,
        studentId: studentID
      })
        .then(data => {
          if (data.info.state) {
            this.detail = data.info.data.studentAnswerAnalysis;
            this.$Message.success(data.info.stateInfo);
            this.shwoDetailDialog = true;
          } else {
            this.$Message.error(data.info.stateInfo);
          }
        })
        .catch(err => {
          this.$Message.error(err);
        });
    },
    toCheckSubject(paperID, studentID, studentNAME) {
      this.studentName = studentNAME;

      this.checkSubject({
        testpaperId: paperID,
        studentId: studentID
      })
        .then(data => {
          if (data.info.state) {
            this.subject = data.info.data.studentAnswerAnalysis;
            this.$Message.success(data.info.stateInfo);
            this.showCheckDialog = true;
          } else {
            this.$Message.error(data.info.stateInfo);
          }
        })
        .catch(err => {
          this.$Message.error(err);
        });
    },
    clear(param) {
      if (param == "preview") {
        this.showPreviewDialog = false;
        this.studentList = [];
        this.haveSubject = null;
      } else if (param == "detail") {
        this.shwoDetailDialog = false;
        this.detail = [];
      } else {
        this.toSubmitCheck();
        this.showCheckDialog = false;
        this.subject = [];
      }
    },
    toSubmitSubjectScore(sId, pId, qId, scr) {
      console.log(sId, pId, qId, scr);
      this.submitSubjectScore({
        studentId: sId, //int
        testpaperId: pId, //int
        teacherJudge: [
          //
          {
            questionId: qId, //int
            socre: scr //double
          }
        ]
      })
        .then(data => {
          if (data.info.state) {
            this.$Message.success(data.info.stateInfo);
          } else {
            this.$Message.error(data.info.stateInfo);
          }
        })
        .catch(err => {
          this.$Message.error(err);
        });
    },
    changeNum(value) {
      this.pageNum = value;
    }
  },
  watch: {
    organization: function() {
      this.getPractice();
    }
  },
  created() {
    this.getPractice();
  }
};
</script>

<style>
.pagepra {
  text-align: center;
  margin: 10px 10px;
}
.unselect {
  -moz-user-select: none;
  -ms-touch-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}

.answer p,
li,
i {
  font-size: 15px;
}
.answer span {
  font-weight: bold;
}
.answer .question-type {
  color: #2d8cf0;
  margin-top: 20px;
}

.answer li {
  list-style: none;
}
.answer .abc {
  display: inline-block;
  padding-left: 9px;
  margin-bottom: 15px;
  margin-right: 30px;
  height: 30px;
  width: 30px;
  font-size: 16px;
  line-height: 30px;
  border: solid #2d8cf0 1px;
  color: #2d8cf0;
  border-radius: 50%;
}
.subjectAnswer span {
  font-weight: bold;
}
hr{
  margin-top: 10px;
  margin-bottom: 10px;
}
</style>



